import React, { Component } from 'react';

import ListaDeRequerimentos from './ListaDeRequerimentos'

export default class EmitirTCE extends Component {
    constructor(){
        super()

        this.state = {
            listaDeApolices : [],
            listaDeRequerimentos : [],
            tce : {
                apolice : {},
                jornada_estagio : {
                    dias_da_semana : 'SEGUNDA_SEXTA',
                    horario_inicio : '08:00',
                    horario_final : '18:00',
                },
            }
        }

        this.onChange = this.onChange.bind(this)
        this.onChangeSubAttribute = this.onChangeSubAttribute.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
    }

    componentWillMount(){
        this.carregaRelacaoDeRequerimentos()
    }

    componentDidMount(){    
        this.carregaRelacaoDeApolices()
        this.carregaValoresIniciais()
    }
    
    criaTermoDeCompromisso(dados){
        const {api} = this.props
        api.TermosDeCompromisso.CriaTermoDeCompromisso(
            dados,
            window.location.href = "#",
            (erro) => console.log(erro)
        )
    }

    carregaRelacaoDeRequerimentos(){
        const {api} = this.props
        api.Requerimentos.ListaRequerimentos(
            //onSuccess
            (listaDeRequerimentos) => this.setState({listaDeRequerimentos}),

            //onError
        )
    }

    carregaRelacaoDeApolices(){
        const {api} = this.props
        api.Apolices.ListaApolices(
            //onSuccess
            (listaDeApolices) => this.setState({listaDeApolices}),
            
            //onError
        )
    }

    carregaValoresIniciais(){
        const provavelAssinatura = new Date()
        provavelAssinatura.setDate(provavelAssinatura.getDate()+2)
        
        const incremento = provavelAssinatura.getDay() === 6 ? 2 : provavelAssinatura.getDay() === 0 ? 1 : 0;
        provavelAssinatura.setDate(provavelAssinatura.getDate()+incremento)
        const data_da_assinatura = provavelAssinatura.toISOString().substring(0,10)
        
        provavelAssinatura.setDate(provavelAssinatura.getDate()+1)
        const inicio = provavelAssinatura.toISOString().substring(0,10)

        provavelAssinatura.setDate(provavelAssinatura.getDate()+60)
        const fim = provavelAssinatura.toISOString().substring(0,10)
        
        this.setState(prevState => ({ 
            tce :  {
                ...prevState.tce,
                data_da_assinatura,
                vigencia:{
                    inicio,
                    fim,
                }
            }
        }))
    }

    onSubmit(e){
        e.preventDefault()

        const {tce} = this.state
        this.criaTermoDeCompromisso(tce)

    }

    onChange(e){
        const {name, value} = e.target

        this.setState(prevState => ({
            tce : {
                ...prevState.tce,
                [name] : value
            }
        }))
    }

    onChangeSubAttribute(attribute, e){
        const {name, value} = e.target

        const target = {
            name : attribute,
            value : {
                ...this.state.tce[attribute],
                [name] : value
            }
        }

        this.onChange({target})
    }

    render() {
        const {tce, listaDeApolices, listaDeRequerimentos} = this.state

        if(tce.requerimento === undefined){
            return (
                <ListaDeRequerimentos 
                    listaDeRequerimentos={listaDeRequerimentos} 
                    onSelect={ (requerimento) => this.setState(prevState => ( {tce : {...prevState.tce, requerimento, carga_horaria_minima : requerimento.aluno.curso.carga_horaria_minima}} )) }
                />
            )
        }

        return (
            <div className="container">
                
                <form className="NovoTCE" onSubmit={this.onSubmit}>
                    <h5 className="my-0 text-dark">
                        <strong>EmissÃ£o de Termo de Compromisso</strong>
                    </h5>
                    
                    <div className="form-row">
                        <div className="form-group col-12">
                            <div>Aluno: {tce.requerimento.aluno.nome}</div>
                            <div>CPF: {tce.requerimento.aluno.cpf}</div>
                            <div>MatrÃ­cula: {tce.requerimento.aluno.matricula}</div>
                            <div>Curso: {tce.requerimento.aluno.curso.nome}</div>
                            <div>Orientador: {tce.requerimento.orientador.nome}</div>
                        </div>
                    </div>
                    
                    <div className="form-row">
                        <div className="form-group col-lg-6 col-md-3 col-sm-6 col-6">
                            <label htmlFor="apolice">Apolice do Seguro para acidentes pessoais</label>
                            <select 
                                className="custom-select"
                                id="id"
                                name="id"
                                value={tce.apolice.id}
                                onChange={ (e) => this.onChangeSubAttribute('apolice', e) }
                                required
                            >
                                <option key='' value=''>Selecione uma apolice</option>
                                {
                                    listaDeApolices.map(apolice => 
                                        <option key={apolice.id} value={apolice.id}>{apolice.empresa}</option>    
                                    )
                                }
                            </select>
                        </div>

                        <div className="form-group col-lg-2 col-md-3 col-sm-6 col-6">
                            <label htmlFor="inicio">Inicio da VigÃªncia</label>
                            <input className="form-control" id="inicio" name="inicio" type="date" value={tce.vigencia.inicio} onChange={(e) => this.onChangeSubAttribute('vigencia', e)} required/>
                        </div>

                        <div className="form-group col-lg-2 col-md-3 col-sm-6 col-6">
                            <label htmlFor="fim">Final da VigÃªncia</label>
                            <input className="form-control" id="fim" name="fim" type="date" value={tce.vigencia.fim} onChange={(e) => this.onChangeSubAttribute('vigencia', e)} required/>
                        </div>

                        <div className="form-group col-lg-2 col-md-3 col-sm-6 col-6">
                            <label htmlFor="carga_horaria_minima">Carga HorÃ¡ria MÃ­nima</label>
                            <input className="form-control" id="carga_horaria_minima" name="carga_horaria_minima" type="number" value={tce.carga_horaria_minima} onChange={this.onChange} required/>
                        </div>
                    </div>
                    <div className="form-row">
                        <div className="form-group col-lg-4 col-md-3 col-sm-6 col-6">
                            <label htmlFor="dias_da_semana">Dias para estÃ¡gio</label>
                            <select 
                                className="custom-select"
                                id="dias_da_semana"
                                name="dias_da_semana"
                                value={tce.jornada_estagio.dias_da_semana}
                                onChange={ (e) => this.onChangeSubAttribute('jornada_estagio', e) }
                            >
                                <option key='SEGUNDA_SEXTA' value='SEGUNDA_SEXTA'>Segunda a Sexta-Feira</option>
                                <option key='SEGUNDA_SABADO' value='SEGUNDA_SABADO'>Segunda a SÃ¡bado</option>
                                <option key='DOMINGO_SABADO' value='DOMINGO_SABADO'>Todos os dias</option>
                            </select>
                        </div>

                        <div className="form-group col-lg-2 col-md-3 col-sm-6 col-6">
                            <label htmlFor="horario_inicio">InÃ­cio</label>
                            <input className="form-control" id="horario_inicio" name="horario_inicio" type="time" min="06:00" max="22:00" value={tce.jornada_estagio.horario_inicio} onChange={(e)=>this.onChangeSubAttribute('jornada_estagio',e)} required/>
                        </div>

                        <div className="form-group col-lg-2 col-md-3 col-sm-6 col-6">
                            <label htmlFor="horario_final">Final</label>
                            <input className="form-control" id="horario_final" name="horario_final" type="time" min="06:00" max="22:00" value={tce.jornada_estagio.horario_final} onChange={(e)=>this.onChangeSubAttribute('jornada_estagio',e)} required/>
                        </div>

                        <div className="form-group col-lg-4 col-md-3 col-sm-6 col-6">
                            <label htmlFor="data_da_assinatura">Data da assinatura do Termo de Compromisso</label>
                            <input className="form-control" id="data_da_assinatura" name="data_da_assinatura" type="date" value={tce.data_da_assinatura} onChange={this.onChange} required/>
                        </div>
                    </div>
                    
                    <div className="form-row bg-light">
                        <div className="py-2 col-lg-3 col-md-4 col-sm-6 col-12">
                            <input className="form-control btn btn-success" type="submit" value="AvanÃ§ar" />
                        </div>
                    </div>
                    
                </form>
            </div>
        )
    }
}
