import React, { Component } from 'react';

export default class ListarRequerimentos extends Component {
    constructor(){
        super()
        this.state = {
            listaDeRequerimentos : []
        }

        this.carregarListaDeRequerimentos = this.carregarListaDeRequerimentos.bind(this)
        this.apagarRequerimento = this.apagarRequerimento.bind(this)
    }

    componentWillMount(){
        this.carregarListaDeRequerimentos()
    }

    carregarListaDeRequerimentos(){
        const {api} = this.props
        api.Requerimentos.ListaRequerimentos(
            (listaDeRequerimentos) => this.setState({listaDeRequerimentos}),
            (erro) => console.log('erro',erro)
        )
    }

    linkParaBaixarRequerimento(id){
        const {api} = this.props
        return `${api.Requerimentos.getBaseUrl()}/${id}/baixar`
    }

    apagarRequerimento(id){
        const {api} = this.props
        api.Requerimentos.RemoveRequerimentoPelaId(id, 
            this.setState(prevState => {
                const listaDeRequerimentos = prevState.listaDeRequerimentos.filter(requerimento => requerimento.id !== id)
                return ({listaDeRequerimentos})
            }),

            (erro) => console.log('erro',erro)
        )
    }

    render() {
        const {listaDeRequerimentos} = this.state

        return (
            <div>
                <h5>RelaÃ§Ã£o de Requerimentos</h5>
                <table className="table table-striped table-hover">
                    <thead>
                        <tr>
                            <th>CPF</th>
                            <th>Nome</th>
                            <th>MatrÃ­cula</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        listaDeRequerimentos.map(requerimento => {
                            const downloadLink = this.linkParaBaixarRequerimento(requerimento.id)
                            return (
                                <tr key={requerimento.id}>
                                    <td>{requerimento.aluno.cpf}</td>
                                    <td>{requerimento.aluno.nome}</td>
                                    <td>{requerimento.aluno.matricula}</td>
                                    <td>
                                        <button onClick={() => window.open(downloadLink, "_blank")}>Baixar</button>
                                        <button onClick={() => this.apagarRequerimento(requerimento.id)}>Apagar</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                    </tbody>
                </table>
            </div>
        )
    }
}
