import {host, doRequest} from './Api'

const path = '/locais_de_estagio'

export const getBaseUrl = () => `${host}${path}`

export function Novo(dados, onSuccess, onError){
    const request = {
        url : `${host}${path}`,
        params : {
            method : 'POST',
            body : JSON.stringify(dados)
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ConsultaPelaId(id, onSuccess, onError){
    const request = {
        url : `${host}${path}/${id}`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ListaTodosAtestadosParaUmCurso(cursoId, onSuccess, onError){
    const request = {
        url : `${host}${path}/${cursoId}/curso_id`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ListaTodos(onSuccess, onError){
    const request = {
        url : `${host}${path}`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function Atualiza(id, dados, onSuccess, onError){
    const request = {
        url : `${host}${path}/${id}`,
        params : {
            method : 'PUT',
            body : JSON.stringify(dados)
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function RemovePelaId(id, onSuccess, onError){
    const request = {
        url : `${host}${path}/${id}`,
        params : {
            method : 'DELETE',
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}