import {host, doRequest} from './Api'

const path = '/requerimentos'

export const getBaseUrl = () => `${host}${path}`

export function CriaRequerimento(dados, onSuccess, onError){
    const request = {
        url : `${host}${path}`,
        params : {
            method : 'POST',
            body : JSON.stringify(dados)
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ConsultaRequerimentoPelaId(id, onSuccess, onError){
    const request = {
        url : `${host}${path}/${id}`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ListaRequerimentos(onSuccess, onError){
    const request = {
        url : `${host}${path}`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function RemoveRequerimentoPelaId(id, onSuccess, onError){
    const request = {
        url : `${host}${path}/${id}`,
        params : {
            method : 'DELETE',
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}