import React from 'react'
import './nav.css'

export function NavBar(props){
    const {menu} = props
    if(menu === undefined){
        return ''
    }
    
    return(
        <ul className="nav">
            {
                Object.keys(menu).map(option => {
                    const item = menu[option]
                    return(
                        <li key={option}>
                            <a href={item.href}>{option}</a>
                            {
                                item.dropdown !== undefined ? 
                                    <div className="nav-dropdown">
                                        { item.dropdown.map(subitem => <a key={subitem.href} href={subitem.href}>{subitem.label}</a>) }
                                    </div>
                                : ''    
                            }
                        </li>        
                    )}
                )
            }
        </ul>
    )
}