import React from 'react'

export default function Login(props){
    const {api} = props

    const usuario = React.createRef()
    const senha = React.createRef()

    const onSubmit = (e) => {
        e.preventDefault()
        const username = usuario.current.value
        const password = senha.current.value

        api.Auth.Login(username, password)
    }

    if(api.Auth.accessToken() !== undefined)
        window.location.href = "#"

    return (
        <div className="container bg-white p-0" style={{minHeight:"100vh"}}>

            <form className="container" onSubmit={onSubmit}>
                <div className="form-group">
                    <label>Usuario</label>
                    <input className="form-control" type="text" ref={usuario}/>
                </div>

                <div className="form-group">
                    <label>Senha</label>
                    <input className="form-control" type="password" ref={senha}/>
                </div>

                <input type="submit" value="Entrar" />
            </form>
        </div>
    )
}

export function Logout(props){
    const {api} = props
    api.Auth.Logout()

    return null
}