import {host, doRequest} from './Api'

const path = '/concedentes'

export const getBaseUrl = () => `${host}${path}`

export function ConsultaConcedentePelaId(id, onSuccess, onError){
    const request = {
        url : `${host}${path}/${id}`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ListaConcedentes(onSuccess, onError){
    const request = {
        url : `${host}${path}`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ListaPropostas(onSuccess, onError){
    const request = {
        url : `${host}${path}/inativos`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function AtualizaConcedente(id, dados, onSuccess, onError){
    const request = {
        url : `${host}${path}/${id}`,
        params : {
            method : 'PUT',
            body : JSON.stringify(dados)
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function RemovePropostaPelaId(id, onSuccess, onError){
    const request = {
        url : `${host}${path}/${id}`,
        params : {
            method : 'DELETE',
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}