import * as Requerimentos from './Requerimentos'
import * as TermosDeCompromisso from './TermosDeCompromisso'
import * as Apolices from './Apolices'
import * as Concedentes from './Concedentes'
import * as CamposDeEstagio from './CamposDeEstagio'
import * as AtestesDeCampo from './AtestesDeCampo'
import * as Cursos from './Cursos'
import * as Auth from './Auth'

export function Api(){
    this.Requerimentos = Requerimentos
    this.TermosDeCompromisso = TermosDeCompromisso
    this.Apolices = Apolices
    this.Concedentes = Concedentes
    this.CamposDeEstagio = CamposDeEstagio
    this.AtestesDeCampo = AtestesDeCampo
    this.Cursos = Cursos
    this.Auth = Auth
}

export const host = 'https://api-estagil-gm.herokuapp.com'

export function doRequest({url='', params={}, onSuccess=(result)=>result, onError=(error)=>error}){
    const defaultRequestParams = {
        method : 'GET',
        headers : {
            "Content-Type": "application/json",
            "Accept": "application/json",
            "Authorization" : `Bearer ${Auth.accessToken()}`
        },
        mode : 'cors',
    }

    const requestParams = { ...defaultRequestParams, ...params }

    fetch(`${url}`, requestParams)
        .then(res => {
            if(res.ok)
                res.json().then(resultado => onSuccess(resultado))
            else if(res.status === 401)
                res.json().then(resultado => 
                    resultado.error === 'invalid_token' ? 
                        Auth.Logout()
                    :
                        Auth.Refresh()
                )
            else
                res.text().then(erros => onError(erros))
        })
        .catch(erros => {
            onError(erros)
        })
}