import React, { Component } from 'react';

import ListaDeConcedentes from './ListaDeConcedentes'
import {Endereco} from './Endereco'
import {RepresentanteLegalPJ} from './RepresentanteLegalPJ'

export default class CadastrarCampoDeEstagio extends Component {
    constructor(){
        super()

        this.state = { 
            listaDeConcedentes : [],

            dados : {
                nome: '',
                telefone: '',
                endereco: {
                    cep: '',
                    logradouro: '',
                    bairro: '',
                    cidade: '',
                    uf: '',
                },
                representante_legal : {
                    nome : '',
                    cargo : '',
                },
            }
        }

        this.carregaListaDeConcedentes = this.carregaListaDeConcedentes.bind(this)
        this.replicarDadosDoConcedente = this.replicarDadosDoConcedente.bind(this)

        this.email = React.createRef()

        this.onChange = this.onChange.bind(this)
        this.onSubmit = this.onSubmit.bind(this)
    }

    componentDidMount(){
        this.carregaListaDeConcedentes()
    }

    carregaListaDeConcedentes(){
        const {api} = this.props
        api.Concedentes.ListaConcedentes(
            //onSuccess
            (listaDeConcedentes) => this.setState({listaDeConcedentes}),

            //onError
        )
    }

    criarCampoDeEstagio(dados){
        const {api} = this.props
        
        api.CamposDeEstagio.Novo(
            dados, 
            window.location.href = ' #',
            (erro) => console.log(erro)
        )
    }

    replicarDadosDoConcedente(){
        const {dados} = this.state
        const {concedente} = dados

        const {nome, telefone, email, representante_legal, endereco} = concedente

        const novosDados = {
            nome, telefone, email, representante_legal, endereco,
        }

        this.setState(prevState => (
            {
                ...prevState,
                dados : {
                    ...prevState.dados,
                    ...novosDados,
                },
                replica_concedente : false,
            }
        ))
    }

    onSubmit(e){
        e.preventDefault()

        const {dados} = this.state
        this.criarCampoDeEstagio(dados)

    }

    onChange(e){
        const {name, value} = e.target

        this.setState(prevState => ({
            dados : {
                ...prevState.dados,
                [name] : value
            }
        }))
    }

    render(){
        const {dados, listaDeConcedentes, replica_concedente} = this.state

        if(dados.concedente === undefined){
            return (
                <ListaDeConcedentes 
                    listaDeConcedentes={listaDeConcedentes} 
                    onSelect={ (concedente) => this.setState(prevState => ( {dados : {...prevState.dados, concedente}} )) }
                />
            )
        }

        const {nome, telefone, email, endereco, representante_legal} = dados

        if(replica_concedente) this.replicarDadosDoConcedente()

        return(
            <form className="container" onSubmit={this.onSubmit}>
                <h5 className="my-1 text-dark">Dados cadastrais do Campo de EstÃ¡gio</h5>

                { 
                    replica_concedente === undefined ?
                        <button onClick={() => this.setState({replica_concedente : true})}>Importar dados do concedente</button> 
                    : 
                        ''
                }

                <div className="form-row">
                    <div className="form-group col-lg-9 col-md-9 col-sm-12 col-12">
                        <label htmlFor="nome">Nome</label>
                        <input className="form-control" id="nome" name="nome" type="text" value={nome} onChange={this.onChange} required minLength="2" maxLength="80" pattern={`^.{2,80}$`}/>
                    </div>
                </div>

                
                <div className="form-row">

                    <div className="form-group col-lg-3 col-md-3 col-sm-12 col-12">
                        <label htmlFor="telefone">Telefone</label>
                        <label className="badge badge-pill badge-info">somente nÃºmeros</label>
                        <input className="form-control" id="telefone" name="telefone" type="text" value={telefone} onChange={this.onChange} required placeholder="somente nÃºmeros" minLength="8" maxLength="11" pattern="^[0-9]{8,11}$"/>
                    </div>

                    <div className="form-group col-lg-5 col-md-5 col-sm-12 col-12">
                        <label htmlFor="email">Email</label>
                        <input className="form-control" id="email" name="email" type="email" defaultValue={email!==undefined ? email : ''} ref={this.email} onChange={this.onChange} minLength="6" maxLength="70"/>
                    </div>        
                </div>

                <Endereco onChange={this.onChange} value={endereco}/>
                <RepresentanteLegalPJ onChange={this.onChange} value={representante_legal} />

                <input type="submit" value="Cadastrar"/>
            </form>
        )
    }
}