import {host, doRequest} from './Api'

const path = '/apolices'

export const getBaseUrl = () => `${host}${path}`

export function CriaApolice(dados, onSuccess, onError){
    const request = {
        url : `${host}${path}`,
        params : {
            method : 'POST',
            body : JSON.stringify(dados)
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ConsultaApolicePelaId(id, onSuccess, onError){
    const request = {
        url : `${host}${path}/${id}`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ListaApolices(onSuccess, onError){
    const request = {
        url : `${host}${path}`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function RemoveApolicePelaId(id, onSuccess, onError){
    const request = {
        url : `${host}${path}/${id}`,
        params : {
            method : 'DELETE',
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}