import React, { Component } from 'react';

export class Navegador extends Component {
        
    constructor(){
        super()
        this.state = {
            rota : ''
        }
    }

    componentWillMount(){
        this.atualizaRota()
        window.onhashchange = (e) => this.atualizaRota()
    }

    atualizaRota(){
        const {hash} = window.location
        const rota = Object.keys(this.props.rotas).includes(hash) ? hash : ''

        this.setState({rota})
    }

    render() {
        const {rota} = this.state
        const Conteudo = this.props.rotas[rota]

        return <Conteudo {...this.props}/>
    }
}