import {doRequest} from './Api'
import {oauthClientConfig} from './oauthClientConfig'

export function Login(username, password) {
    const {clientId, clientSecret} = oauthClientConfig

    const auth =
        (clientId !== undefined && clientSecret !== undefined) ? 
            window.btoa(`${clientId}:${clientSecret}`)
        :
            ''

    const request = {
        url : `http://nixauth.herokuapp.com/oauth/token`,
        params : {
            method : 'POST',
            headers : {
                "Content-Type": "application/x-www-form-urlencoded",
                "Accept": "application/json",
                "Authorization" : `Basic ${auth}`
            },
            mode : 'cors',
            body : `grant_type=password&username=${username}&password=${password}`
        },
        onSuccess: (resultado) => {
            const expires = new Date()
                
            expires.setTime(expires.getTime() + (resultado.expires_in*1000))
            document.cookie = `access_token=${resultado.access_token}; expires=${expires.toUTCString()};`

            expires.setTime(expires.getTime() + (resultado.expires_in*2000))
            document.cookie = `refresh_token=${resultado.refresh_token}; expires=${expires.toUTCString()};`
        },
        onError: (erro) => {
            console.log(erro)
            const expires = new Date()
            document.cookie = `access_token=; expires=${expires.toUTCString()};`
            document.cookie = `refresh_token=; expires=${expires.toUTCString()};`
        }
    }
    
    doRequest(request)
}

export function Logout(){
    const expires = new Date()
    document.cookie = `access_token=; expires=${expires.toUTCString()};`
    document.cookie = `refresh_token=; expires=${expires.toUTCString()};`
}

export function Refresh() {
    const refresh_token = refreshToken()

    const request = {
        url : `http://nixauth.herokuapp.com/oauth/token`,
        params : {
            method : 'POST',
            headers : {
                "Content-Type": "application/x-www-form-urlencoded",
                "Accept": "application/json",
                "Authorization" : `Basic ZXN0YWdpbDplc3RhZ2lsLWJldGE=`
            },
            mode : 'cors',
            body : `grant_type=refresh_token&refresh_token=${refresh_token}`
        },
        onSuccess: (resultado) => {
            const expires = new Date()
                
            expires.setTime(expires.getTime() + (resultado.expires_in*1000))
            document.cookie = `access_token=${resultado.access_token}; expires=${expires.toUTCString()};`

            expires.setTime(expires.getTime() + (resultado.expires_in*2000))
            document.cookie = `refresh_token=${resultado.refresh_token}; expires=${expires.toUTCString()};`
        },
        onError: (erro) => {
            console.log(erro)
            const expires = new Date()
            document.cookie = `access_token=; expires=${expires.toUTCString()};`
            document.cookie = `refresh_token=; expires=${expires.toUTCString()};`
        }
    }
    
    doRequest(request)
}

export function accessTokenAtivo() { return accessToken() !== undefined }

export function refreshTokenAtivo() { return refreshToken() !== undefined }

export function accessToken(){
    const cookies = getCookies()
    return cookies.access_token
}

function refreshToken(){
    const cookies = getCookies()
    return cookies.refresh_token
}



function getCookies(){
    const {cookie} = document

    const cookies = cookie.split(';').reduce((resultado, cookieAtual) => {
        const values = cookieAtual.split('=')
        resultado[values[0]] = values[1]
        return resultado
    }, {})

    return cookies
}

